.project_banner {
    width: 60%;
    margin-bottom: 40px;
    opacity: 0;
    transition: 1.6s;
}

.project_banner.animation {
    opacity: 1;
}

.project_description {
    margin: 30px 0;
    text-align: justify;
    line-height: 160%;
    opacity: 0;
    transform: translate(0, 30px);
    transition: .6s;
}

.project_description.animation {
    opacity: 1;
    transform: translate(0, 0);
}