:root {
  --theme-color: #4E9CF8;
}

.page {
    margin: 120px auto 0;
    width: 480px;
}

.page > .title {
    font-family: 'Samsungsharpsans-medium';    
    display: flex;
    font-size: 32px;
    margin-bottom: 10px;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
}

.page > .title.animation {
    opacity: 1;
    transform: translate(0, 0);    
}
 
.page > .container {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 80px;
}

.main {
    margin-top: 2vh;
    width: 80vw;
    height: auto;
}

.mainlink {
    font-family:'Samsungsharpsans-bold';
    font-size: 3.6vw;
    text-decoration: none;
    color: black;
    opacity: 0;
    transform: translate(-20px, 0);
    transition: 1s;
    transition-delay: 2s;
}
.mainlink:hover {
    text-decoration: underline;
}

.aboutlink {
    position: absolute;
    top: 20vw;
    left: 64vw;
    transition-delay: 2s;
}

.peoplelink {
    position: absolute;
    top: 24.4vw;
    left: 64vw;
    transition-delay: 2.2s;
}

.projectlink {
    position: absolute;
    top: 28.8vw;
    left: 64vw;
    transition-delay: 2.4s;
}

.publicationlink {
    position: absolute;
    top: 33.2vw;
    left: 64vw;
    transition-delay: 2.6s;
}

.courselink {
    position: absolute;
    top: 37.6vw;
    left: 64vw;
    transition-delay: 2.8s;
}


.mainlink.animation {
    opacity: 1;
    transform: translate(0, 0);    
}

.abouts {
    margin-top: 8vh;
    font-size: 16px;
    text-align: justify; 
    line-height: 160%;
    opacity: 0;
    transform: translate(0, 28px);
    transition: .6s;
    transition-delay: .1s;
}

.abouts.animation {
    opacity: 1;
    transform: translate(0, 0);    
}

.banner {
    display: flex;
    position: relative;
    flex-direction: column;
    overflow-x: hidden;
    margin: 12vh 0 10vh;
}

.banner > .image {
    width: 100vw;
}

.banner > .title {
    font-family: 'Samsungsharpsans-medium';
    position: absolute;
    top: 20vw;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, 0%);
    color: white;
    font-size: 6vw;
    /* font-weight: 700; */
    transition: 1.2s;
}

.banner > .title.animation {
    opacity: 1;
    transform: translate(-50%, -50%);
}

.banner > .description {
    font-size: 12px;
    margin-bottom: 8px;
}

.events {
    display: flex;
    flex-wrap: hidden;
    margin: 12px 0;
}

.events > .event {
    margin-right: 20px;
}

.events > .event > .image {
    width: 50px;
}

.person {
    width: 240px;
    padding: 20px 0;
}

.person > .image {
    opacity: 0;
    transform: translate(0, 28px);
    transition: .6s;
}

.person > .image.animation {
    opacity: 1;
    transform: translate(0, 0);    
}

.person > .name {
    font-size: 14px;
    margin: 10px 0 0 0;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
    transition-delay: 0.1s;
}

.person > .name.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.person > .position {
    font-size: 12px;
    margin: 6px 0;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
    transition-delay: 0.2s;
}

.person > .position.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.person > .image {
    width: 180px;
    height: 180px;
    border-radius: 90px;
    object-fit: cover;
}

.alumni > .image {
    filter: grayscale(100%);
}

.page > .selection {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    opacity: 0;
    transform: translate(0, 24px);
    transition: .6s;
}

.page > .selection.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.page > .description {
    font-size: 16px;
    text-align: justify; 
    line-height: 160%;
}


.bibliography {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    font-size: 15px;
    margin-bottom: 32px;
}

.year {
    display: flex;
    font-size: 24px;
    font-weight: 300;
    margin: 24px 0;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
}

.year.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.bibliography > .title {
    font-weight: 600;
    margin-bottom: 4px;
    line-height: 20px;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
}

.bibliography > .title.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.bibliography > .author {
    font-style: italic;
    margin-bottom: 4px;
    line-height: 20px;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
    transition-delay: 0.1s;
}

.bibliography > .author.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.bibliography > .others {
    display: flex;
    margin-bottom: 4px;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;
    transition-delay: 0.1s; 
}

.bibliography > .others.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.bibliography > .others > .other {
    font-size: 13px;
    font-weight: 700;
}

.bibliography > .others > .other > a {
    text-decoration: none;
    color: var(--theme-color);
}

.bibliography > .others > .other > a:visited {
    text-decoration: none;
}


.project {
    display: flex;
    align-items: center;
    margin: 60px 0 160px;
}

.project > .contents {
    width: 240px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    margin-bottom: 40px;
}

.project > .contents > .title {
    font-family: 'Samsungsharpsans-medium';
    display: flex;
    font-weight: 700;
    font-size: 24px;
    color: var(--theme-color);
    margin: 40px 0 10px;
    opacity: 0;
    transform: translate(0, 50%);
    transition: .6s;   
}

.project > .contents > .title.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.project > .contents > .line {
    opacity: 0;
    border: 2px solid var(--theme-color);
    margin-bottom: 30px;
    margin-left:0;
    transition: .8s;   
}

.project > .contents > .line.animation {
    opacity: 1;
    width: 220px;
}

.project > .contents > .description {
    font-size: 16px;
    text-align: left;
    line-height: 150%;
    margin-bottom: 30px;
    opacity: 0;
    transform: translate(0, -18px);
    transition: .8s;
    transition-delay: 0.4s;
}

.project > .contents > .description.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.project > .contents > .more_holder {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.project > .contents > .more_holder > .more {
    text-decoration: none;
    color: var(--theme-color);
    font-weight: 700;
    font-size: 16px;
    opacity: 0;
    transform: translate(-20px, 0);
    transition: .7s;
    transition-delay: 0.6s;
}

.project > .contents > .more_holder > .more.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.project > .image {
    width: 220px;
    height: 153.04px;
    opacity: 0;
    transition: 1.2s;
}

.project > .image.animation {
    opacity: 1;
}

.project > .right {
    margin-left: 20px;
}

.project > .left {
    margin-right: 20px;
}

.course {
    margin-top: 60px;
    text-align: left;
}

.course > .title {
    font-family: 'Samsungsharpsans-medium';
    font-size: 20px;
    margin-bottom: 8px;
    opacity: 0;
    transform: translate(0, 10px);
    transition: .8s;
    transition-delay: 0.1s;
}

.course > .title.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.course > .description {
    opacity: 0;
    transform: translate(0, 10px);
    transition: .8s;
    transition-delay: 0.2s;
}

.course > .description.animation {
    opacity: 1;
    transform: translate(0, 0);    
}

.periodlist {
    margin-top: 8px;
    opacity: 0;
    transform: translate(0, 10px);
    transition: .8s;
    transition-delay: 0.3s;
}

.periodlist.animation {
    opacity: 1;
    transform: translate(0, 0);     
}

.period {
    margin-top: 4px;
}



@media (min-width:992px) {
    .page {
        width: 720px;
    }
    .project > .contents {
        width: 340px;
    }
    .project > .contents > .line.animation {
        opacity: 1;
        width: 340px;
    }
    .project > .image {
        margin-top: 60px;
        width: 340px;
        height: 236.52px;
    }
}

@media (min-width:1200px) {
    .page {
        width: 960px;
    }
    .project > .contents {
        width: 460px;
    }
    .project > .contents > .line.animation {
        opacity: 1;
        width: 460px;
    }

    .project > .image {
        margin-top: 0px;
        width: 460px;
        height: 320px;
    }
}