:root {
    --theme-color: #4E9CF8;
}

.navbar {
    width: 100vw;
    z-index: 1;
    height: 60px;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    opacity: 0.95;
}

.navbarcontent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
}

.navbarcontent > .logo {
    display: flex;
    align-items: flex-end;
    text-decoration: none;
    color: black;
    font-size: 18px;
}

.navbarcontent > .logo:hover {
    text-decoration: none;
}

.navbarcontent > .logo:visited {
    text-decoration: none;
}

.navbarcontent > .logo > .image {
    width: 44px;
    padding-bottom: 3px;
}

@media (min-width:546px) {
    .navbarcontent {
        width: 526px;
    }
}

@media  (min-width:768px) {
    .navbarcontent {
        width: 748px;
    }
}
@media (min-width:992px) {
    .navbarcontent {
        width: 972px;
    }
}

@media (min-width:1200px) {
    .navbarcontent {
        width: 1180px;
    }
}

.links > .navBtn {
    margin: 0 10px;
    text-decoration: none;
    color: black;
}

.links > .navBtn > span:hover {
    text-decoration: none;
    color: var(--theme-color);
}

.links > .navBtn > span:visited {
    text-decoration: none;
}